.programacao {
    background-color: $cor-section1;
    padding-bottom: 4rem;

        .prog__cabecalho {
            display: flex;
            flex-wrap: wrap;
            gap: 2rem;
            justify-content: center;
            align-items: center;
        }

        .prog__titulo {
            font-family: $fonte-titulo;
            font-size: 5rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 4rem;
            padding: 2rem;
            color: #fff;

                @include celular {
                    font-size: 3rem;
                    margin-bottom: 1rem;
                }
        }

        .prog__cards {
            display: flex;
            flex-wrap: wrap;
            gap: 5rem;
            justify-content: center;
            align-items: center;
        }

        .prog__card {
            text-align: center;
            width: 550px;
            
        }

        .prog__card__titulo {
            font-family: $fonte-corpo;
            font-size: 3rem;
            color: #fff;
            box-shadow: 0 0 10px 0 #fff;
            cursor: pointer;
            background-color: $cor-section2;
            width: 340px;
            margin: 0 auto;
        }

        .tabela__prog {
            padding-top: 2rem;
            margin: 0 auto;
            width: 34rem;

            @include celular {
                width: 340px;
            }
        }

        .tabela__info__header {
            font-family: $fonte-corpo;
            font-weight: bold;
            font-size: 1.6rem;
            color: #fff;
        }

        .tabela__prog__body {

            &:hover {
                background-color: #fff;
                transition: all .5s;
            }

            &:hover .tabela__prog__info {
                color: #000;
                transition: all .5s;
            }
        }

       

        .tabela__prog__info {
            border-bottom: solid 1px #000;
            border-collapse: collapse;
            font-family: $fonte-corpo;
            font-weight: bold;
            padding: .4rem;
            font-size: 1rem;
            color: #fff;
        }

        
}